/* ===== RESPONSIVE UTILITIES FOR HOW TO USE SECTION ===== */

/* Device detection utility classes */
.howto-slide.mobile {
  --step-gap: var(--space-sm);
  --step-padding: var(--space-sm);
  --container-max-width: 100%;
}

.howto-slide.tablet {
  --step-gap: var(--space-md);
  --step-padding: var(--space-md);
  --container-max-width: 700px;
}

.howto-slide.desktop {
  --step-gap: var(--space-lg);
  --step-padding: var(--space-lg);
  --container-max-width: 800px;
}

/* Dynamic spacing based on device class */
.howto-slide .steps-container {
  gap: var(--step-gap, var(--space-md));
  max-width: var(--container-max-width, 800px);
}

.howto-slide .step-item {
  padding: var(--step-padding, var(--space-md));
}

/* Enhanced scroll behavior for different devices */
.howto-slide.mobile .steps-container {
  scroll-behavior: smooth;
  scroll-snap-type: y proximity;
}

.howto-slide.mobile .step-item {
  scroll-snap-align: start;
}

/* Improved touch targets for mobile */
.howto-slide.mobile .step-item {
  min-height: 44px; /* iOS recommendation for touch targets */
  cursor: pointer;
}

.howto-slide.mobile .next-section-arrow {
  bottom: var(--space-md);
  padding: var(--space-sm);
  border-radius: 50%;
  background: rgba(var(--accent-primary-rgb), 0.1);
  backdrop-filter: blur(8px);
}

/* Optimize for tablet landscape */
.howto-slide.tablet {
  justify-content: center;
}

.howto-slide.tablet .steps-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-md);
  align-items: start;
}

.howto-slide.tablet .step-item {
  margin-bottom: 0;
}

/* Enhanced desktop experience */
.howto-slide.desktop .step-item {
  transition: all 0.3s ease, transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.howto-slide.desktop .step-item:hover {
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  z-index: 10;
  position: relative;
}

/* Performance optimizations */
.howto-slide .step-item {
  contain: layout style paint;
  will-change: transform;
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  .howto-slide * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus states for keyboard navigation */
.step-item:focus-visible {
  outline: 2px solid var(--accent-primary);
  outline-offset: 2px;
  border-color: var(--accent-primary);
}

/* Dark mode optimizations */
@media (prefers-color-scheme: dark) {
  .step-item {
    backdrop-filter: blur(12px);
    background: rgba(var(--bg-card-rgb), 0.8);
  }
}

/* Print styles */
@media print {
  .howto-slide {
    background: none !important;
    color: black !important;
    page-break-inside: avoid;
  }
  
  .step-item {
    background: none !important;
    border: 1px solid #ccc !important;
    break-inside: avoid;
    margin-bottom: 1rem !important;
  }
  
  .next-section-arrow {
    display: none !important;
  }
}
