/* ===== COMPREHENSIVE SCROLL SNAP SYSTEM ===== */

/* Root scroll container */
html {
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100%;
}

body {
  height: 100%;
  overflow-x: hidden;
}

.App {
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
  height: 100dvh; /* Dynamic viewport height for mobile */
}

/* ===== SLIDE SYSTEM ENHANCED ===== */

.slide {
  min-height: 100vh;
  min-height: 100dvh;
  scroll-snap-align: start;
  scroll-snap-stop: always;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden; /* Prevent horizontal scroll */
}

/* Ensure slides fill viewport properly */
.slide:first-of-type {
  scroll-snap-align: start;
}

.slide:last-of-type {
  scroll-snap-align: end;
}

/* ===== DEVICE-SPECIFIC SCROLL OPTIMIZATIONS ===== */

/* Mobile Portrait (320px - 767px) */
@media (max-width: 767px) {
  html {
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch; /* iOS momentum scrolling */
  }
  
  .App {
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch;
  }
  
  .slide {
    scroll-snap-align: start;
    scroll-snap-stop: always;
    /* Ensure proper spacing on mobile */
    padding: var(--space-md) var(--space-sm);
    min-height: 100vh;
    min-height: 100dvh;
  }
  
  /* Optimize touch scrolling on mobile */
  .slide {
    touch-action: pan-y;
  }
}

/* Mobile Landscape (480px - 767px, landscape) */
@media (max-width: 767px) and (orientation: landscape) {
  .slide {
    min-height: 100vh;
    scroll-snap-align: start;
    padding: var(--space-sm) var(--space-sm);
  }
}

/* Tablet (768px - 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
  html {
    scroll-snap-type: y mandatory;
  }
  
  .slide {
    scroll-snap-align: start;
    scroll-snap-stop: always;
    padding: var(--space-lg) var(--space-md);
  }
  
  /* Better tablet scrolling */
  .App {
    scroll-padding-top: 60px; /* Account for navigation */
  }
}

/* Desktop (1024px+) */
@media (min-width: 1024px) {
  html {
    scroll-snap-type: y mandatory;
  }
  
  .slide {
    scroll-snap-align: start;
    scroll-snap-stop: always;
    padding: var(--space-xl) var(--space-md);
  }
  
  .App {
    scroll-padding-top: 80px; /* Account for larger navigation */
  }
}

/* Large Desktop (1440px+) */
@media (min-width: 1440px) {
  .slide {
    padding: var(--space-xxl) var(--space-lg);
  }
}

/* ===== SCROLL SNAP POLYFILL FOR OLDER BROWSERS ===== */

/* Fallback for browsers without scroll-snap support */
@supports not (scroll-snap-type: y mandatory) {
  html {
    scroll-behavior: smooth;
  }
  
  .slide {
    margin-bottom: 0;
  }
}

/* ===== ACCESSIBILITY AND PERFORMANCE ===== */

/* Disable scroll snap for users who prefer reduced motion */
@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
    scroll-snap-type: none;
  }
  
  .App {
    scroll-snap-type: none;
  }
  
  .slide {
    scroll-snap-align: none;
    scroll-snap-stop: normal;
  }
}

/* High refresh rate optimization */
@media (min-resolution: 120dpi) {
  html {
    scroll-behavior: smooth;
  }
  
  .slide {
    will-change: scroll-position;
  }
}

/* ===== TOUCH DEVICE OPTIMIZATIONS ===== */

/* Touch devices get enhanced scrolling */
@media (hover: none) and (pointer: coarse) {
  html {
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch;
    overscroll-behavior: contain;
  }
  
  .App {
    -webkit-overflow-scrolling: touch;
    overscroll-behavior-y: contain;
  }
  
  .slide {
    touch-action: pan-y;
    scroll-snap-align: start;
    scroll-snap-stop: always;
  }
}

/* ===== KEYBOARD NAVIGATION SUPPORT ===== */

/* Ensure proper focus management with scroll snap */
.slide:focus-within {
  scroll-snap-align: start;
}

/* Smooth scrolling for keyboard navigation */
.slide:target {
  scroll-snap-align: start;
}

/* ===== SECTION-SPECIFIC SCROLL ADJUSTMENTS ===== */

/* Hero section - always snap to start */
.hero-slide {
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

/* Features section */
.features-slide {
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

/* How to use section */
.howto-slide {
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

/* Benefits section */
.benefits-slide {
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

/* Technologies section */
.technologies-slide {
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

/* Download section */
.download-slide {
  scroll-snap-align: start;
  scroll-snap-stop: always;
}

