/* ===== MOBILE AND DEVICE-SPECIFIC SCROLL SNAP ENHANCEMENTS ===== */

/* Enhanced scroll container for all devices */
.scroll-snap-container {
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
  height: 100dvh;
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
}

/* Mobile-specific scroll optimizations */
@media (max-width: 767px) {
  .scroll-snap-container {
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch;
    overscroll-behavior-y: contain;
    scroll-padding-top: 60px; /* Account for mobile navigation */
  }
  
  /* Enhanced mobile slide behavior */
  .slide {
    scroll-snap-align: start;
    scroll-snap-stop: always;
    min-height: 100vh;
    min-height: 100dvh;
    padding: var(--space-sm) var(--space-xs);
    touch-action: pan-y;
  }
  
  /* Mobile landscape adjustments */
  @media (orientation: landscape) {
    .scroll-snap-container {
      scroll-padding-top: 50px;
    }
    
    .slide {
      min-height: 100vh;
      padding: var(--space-xs) var(--space-xs);
    }
  }
  
  /* Optimize for small screens */
  @media (max-height: 600px) {
    .slide {
      min-height: 100vh;
      justify-content: flex-start;
      padding-top: calc(var(--space-sm) + 60px);
    }
  }
}

/* Tablet optimizations */
@media (min-width: 768px) and (max-width: 1023px) {
  .scroll-snap-container {
    scroll-snap-type: y mandatory;
    scroll-padding-top: 70px;
  }
  
  .slide {
    scroll-snap-align: start;
    scroll-snap-stop: always;
    padding: var(--space-md) var(--space-sm);
  }
  
  /* Tablet landscape specific */
  @media (orientation: landscape) {
    .slide {
      padding: var(--space-sm) var(--space-md);
    }
  }
}

/* Desktop optimizations */
@media (min-width: 1024px) {
  .scroll-snap-container {
    scroll-snap-type: y mandatory;
    scroll-padding-top: 80px;
  }
  
  .slide {
    scroll-snap-align: start;
    scroll-snap-stop: always;
    padding: var(--space-xl) var(--space-md);
  }
}

/* ===== DEVICE-SPECIFIC SCROLL BEHAVIORS ===== */

/* iOS Safari specific fixes */
@supports (-webkit-touch-callout: none) {
  .scroll-snap-container {
    height: 100vh;
    height: -webkit-fill-available;
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch;
  }
  
  .slide {
    min-height: 100vh;
    min-height: -webkit-fill-available;
  }
}

/* Chrome mobile address bar compensation */
@media screen and (max-device-width: 767px) {
  .scroll-snap-container {
    height: 100vh;
    height: 100dvh;
  }
  
  .slide {
    min-height: 100vh;
    min-height: 100dvh;
  }
}

/* High DPI displays */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .scroll-snap-container {
    scroll-behavior: smooth;
  }
}

/* Touch devices enhancement */
@media (hover: none) and (pointer: coarse) {
  .scroll-snap-container {
    scroll-snap-type: y mandatory;
    -webkit-overflow-scrolling: touch;
    overscroll-behavior: contain;
    scroll-behavior: smooth;
  }
  
  .slide {
    touch-action: pan-y;
    scroll-snap-align: start;
    scroll-snap-stop: always;
  }
  
  /* Enhance touch scrolling performance */
  .slide {
    contain: layout style;
    will-change: scroll-position;
  }
}

/* ===== SECTION-SPECIFIC MOBILE OPTIMIZATIONS ===== */

/* Hero section mobile */
@media (max-width: 767px) {
  .hero-slide {
    scroll-snap-align: start;
    padding: var(--space-md) var(--space-sm);
    text-align: center;
    justify-content: center;
  }
  
  .hero-multimedia-slide {
    scroll-snap-align: start;
    padding: var(--space-sm) var(--space-xs);
  }
}

/* Features section mobile grid */
@media (max-width: 767px) {
  .features-slide {
    scroll-snap-align: start;
    padding: var(--space-md) var(--space-sm);
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: var(--space-sm);
  }
}

/* How to use section mobile */
@media (max-width: 767px) {
  .howto-slide {
    scroll-snap-align: start;
    padding: var(--space-md) var(--space-sm);
  }
  
  .howto-slide.mobile .steps-container {
    gap: var(--space-sm);
  }
  
  .howto-slide.mobile .step-item {
    margin-bottom: var(--space-xs);
  }
}

/* Benefits section mobile */
@media (max-width: 767px) {
  .benefits-slide {
    scroll-snap-align: start;
    padding: var(--space-md) var(--space-sm);
  }
  
  .benefits-grid {
    grid-template-columns: 1fr;
    gap: var(--space-sm);
  }
}

/* Technologies section mobile */
@media (max-width: 767px) {
  .technologies-slide {
    scroll-snap-align: start;
    padding: var(--space-md) var(--space-sm);
  }
  
  .technologies-grid {
    grid-template-columns: 1fr;
    gap: var(--space-sm);
  }
}

/* Download section mobile */
@media (max-width: 767px) {
  .download-slide {
    scroll-snap-align: start;
    padding: var(--space-lg) var(--space-sm);
    text-align: center;
  }
}

/* ===== SCROLL PERFORMANCE OPTIMIZATIONS ===== */

/* Optimize scroll rendering performance */
.scroll-snap-container {
  contain: layout style;
}

.slide {
  contain: layout style paint;
  content-visibility: auto;
  contain-intrinsic-size: 100vh;
}

/* GPU acceleration for smooth scrolling */
.scroll-snap-container,
.slide {
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

/* ===== ACCESSIBILITY ENHANCEMENTS ===== */

/* Respect reduced motion preferences */
@media (prefers-reduced-motion: reduce) {
  .scroll-snap-container {
    scroll-behavior: auto;
    scroll-snap-type: none;
  }
  
  .slide {
    scroll-snap-align: none;
    scroll-snap-stop: normal;
  }
}

/* Focus management for keyboard users */
.slide:focus-within {
  scroll-snap-align: start;
}

/* ===== NAVIGATION ENHANCEMENTS ===== */

/* Mobile navigation adjustments */
@media (max-width: 767px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: rgba(var(--bg-primary), 0.95);
    backdrop-filter: blur(10px);
  }
  
  .scroll-snap-container {
    padding-top: 0;
  }
  
  .slide:first-child {
    padding-top: calc(var(--space-md) + 60px);
  }
}

/* Tablet navigation */
@media (min-width: 768px) and (max-width: 1023px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: rgba(var(--bg-primary), 0.95);
    backdrop-filter: blur(10px);
  }
  
  .slide:first-child {
    padding-top: calc(var(--space-lg) + 70px);
  }
}

/* ===== INTERACTION IMPROVEMENTS ===== */

/* Smooth momentum scrolling on all devices */
.scroll-snap-container {
  scroll-behavior: smooth;
  scroll-timeline: --page-scroll block;
}

/* Better scroll indicators */
.scroll-snap-container::-webkit-scrollbar {
  width: 8px;
}

.scroll-snap-container::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
}

.scroll-snap-container::-webkit-scrollbar-thumb {
  background: var(--accent-primary);
  border-radius: 4px;
}

.scroll-snap-container::-webkit-scrollbar-thumb:hover {
  background: var(--accent-secondary);
}

/* Hide scrollbar on mobile */
@media (max-width: 767px) {
  .scroll-snap-container::-webkit-scrollbar {
    display: none;
  }
  
  .scroll-snap-container {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
}
