/* ===== HOW TO USE SECTION - RESPONSIVE STYLES ===== */

/* Base styles enhancement */
.howto-slide {
  background: radial-gradient(circle at 50% 50%, var(--bg-secondary) 0%, var(--bg-primary) 100%);
  padding: var(--space-xl) var(--space-md);
  min-height: 100vh;
  min-height: 100dvh; /* Dynamic viewport height for mobile */
  justify-content: flex-start;
  padding-top: calc(var(--space-xl) + 60px);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow-x: hidden;
}

.steps-container {
  max-width: 800px;
  width: 100%;
  margin-bottom: var(--space-lg);
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  flex: 1;
  justify-content: center;
}

.step-item {
  display: flex;
  align-items: flex-start;
  gap: var(--space-lg);
  background: var(--bg-card);
  backdrop-filter: blur(8px);
  padding: var(--space-md);
  border-radius: 12px;
  border: 1px solid var(--border-subtle);
  transition: all var(--transition-base);
  position: relative;
  overflow: hidden;
}

.step-item:hover {
  border-color: var(--border-active);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.step-number {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--accent-primary);
  min-width: 3rem;
  font-family: var(--font-mono);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  background: rgba(var(--accent-primary-rgb), 0.1);
  border-radius: 8px;
  flex-shrink: 0;
}

.step-content {
  flex: 1;
  min-width: 0; /* Prevents flex item from overflowing */
}

.step-icon {
  font-size: 1.25rem;
  margin-bottom: var(--space-xs);
  display: block;
  line-height: 1;
}

.step-item h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: var(--space-sm);
  color: var(--text-primary);
  line-height: 1.3;
  transition: all var(--transition-base);
}

.step-item p {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.95rem;
  margin: 0;
  transition: all var(--transition-base);
}

.step-item:hover h3 {
  color: var(--accent-color, var(--accent-primary));
  transform: translateY(-2px);
}

.step-item:not(:hover) h3 {
  color: var(--text-primary);
  transform: translateY(0);
}

.step-item:hover p {
  color: var(--text-primary);
}

.step-item:not(:hover) p {
  color: var(--text-secondary);
}

.step-item:hover .step-number {
  background: var(--accent-color, var(--accent-primary));
  color: white;
  transform: scale(1.05);
}

.step-item:not(:hover) .step-number {
  background: rgba(var(--accent-primary-rgb), 0.1);
  color: var(--accent-primary);
  transform: scale(1);
}

/* ===== RESPONSIVE BREAKPOINTS ===== */

/* Large Desktop (1440px+) */
@media (min-width: 1440px) {
  .howto-slide {
    padding: var(--space-xxl) var(--space-lg);
  }
  
  .steps-container {
    max-width: 900px;
    gap: var(--space-lg);
  }
  
  .step-item {
    padding: var(--space-lg);
    gap: var(--space-xl);
  }
  
  .step-number {
    font-size: 1.75rem;
    min-width: 3.5rem;
    height: 3.5rem;
  }
  
  .step-item h3 {
    font-size: 1.75rem;
  }
  
  .step-item p {
    font-size: 1rem;
  }
}

/* Desktop (1024px - 1439px) */
@media (min-width: 1024px) and (max-width: 1439px) {
  .howto-slide {
    padding: var(--space-xl) var(--space-md);
  }
  
  .steps-container {
    max-width: 800px;
  }
}

/* Tablet Landscape (768px - 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
  .howto-slide {
    padding: var(--space-lg) var(--space-sm);
    padding-top: calc(var(--space-lg) + 60px);
  }
  
  .steps-container {
    max-width: 700px;
    gap: var(--space-sm);
  }
  
  .step-item {
    padding: var(--space-sm);
    gap: var(--space-md);
  }
  
  .step-number {
    font-size: 1.25rem;
    min-width: 2.5rem;
    height: 2.5rem;
  }
  
  .step-item h3 {
    font-size: 1.25rem;
  }
  
  .step-item p {
    font-size: 0.9rem;
  }
}

/* Mobile Landscape (480px - 767px) */
@media (min-width: 480px) and (max-width: 767px) {
  .howto-slide {
    padding: var(--space-md) var(--space-xs);
    padding-top: calc(var(--space-md) + 50px);
  }
  
  .steps-container {
    max-width: 100%;
    gap: var(--space-sm);
  }
  
  .step-item {
    padding: var(--space-sm);
    gap: var(--space-sm);
    flex-direction: column;
    text-align: center;
  }
  
  .step-number {
    font-size: 1.25rem;
    min-width: 2.5rem;
    height: 2.5rem;
    align-self: center;
  }
  
  .step-content {
    text-align: center;
  }
  
  .step-item h3 {
    font-size: 1.25rem;
    margin-bottom: var(--space-xs);
  }
  
  .step-item p {
    font-size: 0.9rem;
    line-height: 1.5;
  }
  
  /* Enhance mobile hover effects */
  .step-item:hover {
    transform: translateY(-4px);
  }
}

/* Mobile Portrait (320px - 479px) */
@media (max-width: 479px) {
  .howto-slide {
    padding: var(--space-sm) var(--space-xs);
    padding-top: calc(var(--space-sm) + 50px);
  }
  
  .steps-container {
    gap: var(--space-xs);
  }
  
  .step-item {
    padding: var(--space-xs) var(--space-sm);
    gap: var(--space-xs);
    flex-direction: column;
    text-align: center;
    border-radius: 8px;
  }
  
  .step-number {
    font-size: 1rem;
    min-width: 2rem;
    height: 2rem;
    align-self: center;
  }
  
  .step-icon {
    font-size: 1rem;
    margin-bottom: 0.25rem;
  }
  
  .step-item h3 {
    font-size: 1.125rem;
    margin-bottom: 0.25rem;
    line-height: 1.2;
  }
  
  .step-item p {
    font-size: 0.875rem;
    line-height: 1.4;
  }
  
  /* Simplified mobile interactions */
  .step-item:hover {
    transform: scale(1.02);
    border-color: var(--accent-primary);
  }
}

/* ===== DEVICE-SPECIFIC OPTIMIZATIONS ===== */

/* Touch devices */
@media (hover: none) and (pointer: coarse) {
  .step-item:hover {
    transform: none;
  }
  
  .step-item:active {
    transform: scale(0.98);
    border-color: var(--accent-primary);
  }
  
  .step-item h3 {
    transition: none;
  }
  
  .step-item p {
    transition: none;
  }
}

/* High DPI displays */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .step-item {
    border-width: 0.5px;
  }
}

/* Landscape orientation on mobile */
@media (max-width: 767px) and (orientation: landscape) {
  .howto-slide {
    min-height: 100vh;
    padding-top: calc(var(--space-sm) + 40px);
  }
  
  .steps-container {
    justify-content: flex-start;
    padding-top: var(--space-xs);
  }
  
  .step-item {
    flex-direction: row;
    text-align: left;
    padding: var(--space-xs) var(--space-sm);
  }
  
  .step-number {
    align-self: flex-start;
  }
  
  .step-content {
    text-align: left;
  }
}

/* Reduce motion for accessibility */
@media (prefers-reduced-motion: reduce) {
  .step-item {
    transition: none;
  }
  
  .step-item:hover {
    transform: none;
  }
  
  .step-item h3,
  .step-item p {
    transition: none;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .step-item {
    border-width: 2px;
    border-color: var(--text-primary);
  }
  
  .step-item:hover {
    border-color: var(--accent-primary);
    background: var(--bg-primary);
  }
  
  .step-number {
    border: 2px solid var(--accent-primary);
  }
}
